@custom-media --narrow-vp (width <= 767.9px);
@custom-media --not-narrow-vp (width >= 768px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --wide-vp (width >= 1280px);
@custom-media --not-wide-vp (width <= 1279.9px);
:root {
  --white-color: #ffffff;
  --white-trans-color: rgba(255, 255, 255, 0.3);
  --black-color: #000000;
  --accent-color: #68b738;
  --accent-trans-color: rgba(104, 183, 56, 0.85);
  --dark-accent-color: #5eaa2f;
  --error-color: #ff8282;

  --dark-form-text-color: #222222;
  --form-color: #444444;
  --light-form-color: #cdcdcd;
  --slider-track-color: #dcdcdc;

  --border-color: #d9d9d9;
  --form-border-color: #e7e7e7;
  --main-border-color: #e6e6e6;
  --catalog-more-color: #d3d3d3;

  --footer-bg: #f2f2f2;
  --section-bg-color: #eaeaea;

  --button-color: #ebebeb;

  --social__link-color: #666666;
  --radio-hover: #bababa;

  --window-version: "mobile";

  @media (--not-narrow-vp) {
    --window-version: "tablet";
  }
}
@mixin word-font ($font, $font-size, $line-height) {
  font-family: $font, "Helvetica", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: $font-size;
  line-height: $line-height;
}
@mixin list-zero {
  margin: 0;
  padding: 0;
  list-style-type: none;
}
@font-face {
  font-family: "Oswald";
  font-style: medium;
  font-weight: 500;
  src:
    url("../fonts/oswaldmedium.woff2") format("woff2"),
    url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src:
    url("../fonts/oswaldregular.woff2") format("woff2"),
    url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}
.page {
  height: 100%;
}
.page__body {
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  font-family: "Arial", "Helvetica", sans-serif;
  background: var(--white-color);
  color: var(--black-color);
  min-width: 320px;
  overflow-x: hidden;
}
.page__centerer {
  margin: 0 20px;
  box-sizing: border-box;

  @media (--not-narrow-vp) {
    margin: 0 30px;
  }

  @media (--wide-vp) {
    margin: 0 auto;
    width: 1220px;
  }
}
.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: none;
  clip: rect(0 0 0 0);
}
.header {
  background-color: var(--white-color);
  color: var(--black-color);
}
.header__container {
  box-sizing: border-box;
  min-height: 66px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;

  @media (--not-narrow-vp) {
    flex-direction: row;
    min-height: 107px;
  }

  @media (--wide-vp) {
    min-height: 165px;
    box-sizing: border-box;
  }
}
.header__logo {
  z-index: 100;
  display: flex;
  align-items: center;
  min-height: 65px;

  &:hover {
    opacity: 0.8;
    transition: opacity 0.2s ease;
  }

  &:not(:hover) {
    transition: opacity 0.2s ease;
  }

  &:active {
    opacity: 0.6;
  }

  &:focus-visible {
    border: none;
    border-radius: 2px;
    outline: 2px solid var(--accent-color);
    outline-offset: 2px;
  }
}
.header__logo img {
  @media (--not-narrow-vp) {
    width: 174px;
    height: 52px;
  }

  @media (--wide-vp) {
    width: 202px;
    height: 60px;
  }
}
.header__logo-none {
  display: flex;
  align-items: center;
  width: 101px;
  height: 18px;

  @media (--not-narrow-vp) {
    display: none;
  }
}
.header__logo-none img {
  margin-left: -17px;
  width: 101px;
  height: 18px;
}
.navigation__button {
  position: absolute;
  top: 23px;
  right: 20px;
  width: 24px;
  height: 18px;
  box-sizing: border-box;
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;

  @media (--not-narrow-vp) {
    display: none;
  }
}
.navigation--closed .navigation__button::after {
  content: "";
  width: 24px;
  height: 2px;
  background-color: var(--black-color);
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 8px 0 0 var(--black-color), 0 16px 0 0 var(--black-color);
}
.navigation--opened .navigation__button {
  &::before,
  &::after {
    content: "";
    width: 25px;
    height: 2px;
    background-color: var(--black-color);
    position: absolute;
    top: 6px;
    left: 0;
  }

  &::before {
    transform: rotate(45deg);
  }

  &::after {
    transform: rotate(-45deg);
  }
}
@media (--narrow-vp) {
  .navigation--closed .navigation__container {
    display: none;
  }

  .navigation--opened .navigation__container {
    display: block;
  }

  .navigation--nojs {
    width: 100%;
  }

  .navigation--nojs .navigation__container {
    position: static;
    display: block;
  }

  .navigation--nojs .navigation__button {
    display: none;
  }
}
.navigation__list {
  @include list-zero;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  background-color: var(--white-color);

  @media (--not-narrow-vp) {
    background-color: transparent;
    flex-direction: row;
    column-gap: 33px;
    justify-content: space-between;
  }
}
.navigation__link {
  text-decoration: none;
  display: block;
  padding: 22px 20px 24px 20px;
  color: var(--black-color);
  text-transform: uppercase;
  position: relative;

  @include word-font ("Oswald", 20px, 20px);
  font-weight: 500;

  @media (--not-narrow-vp) {
    padding-left: 0;
    padding-right: 0;
    padding-top: 10px;
    padding-bottom: 10px;

    @include word-font ("Oswald", 18px, 24px);
  }

  @media (--wide-vp) {
    color: var(--black-color);

    @include word-font ("Oswald", 20px, 30px);
  }

  &:hover {
    @media (--not-narrow-vp) {
      opacity: 0.6;
      transition: opacity 0.4s ease;
    }
  }

  &:not(:hover) {
    @media (--not-narrow-vp) {
      transition: opacity 0.4s ease;
    }
  }

  &:active {
    @media (--not-narrow-vp) {
      opacity: 0.3;
    }
  }

  &:focus-visible {
    border-radius: 2px;
    outline: 2px solid var(--dark-form-text-color);
    outline-offset: 3px;
  }
}
.navigation__link--active {
  &::before {
    @media (--not-narrow-vp) {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      bottom: 0;
      background-color: var(--accent-color);
    }

    @media (--wide-vp) {
      content: "";
      position: absolute;
      width: 100%;
      height: 2px;
      background-color: var(--accent-color);
    }
  }
}
.navigation__item {
  @media (--narrow-vp) {
    width: 100%;
    text-align: center;
    border-top: 1px solid var(--main-border-color);

    &:last-child {
      border-bottom: 1px solid var(--main-border-color);
    }
  }
}
.navigation__link--white {
  @media (--wide-vp) {
    color: var(--white-color);
  }

  &::before {
    background-color: var(--accent-color);

    @media (--wide-vp) {
      background-color: var(--white-color);
    }
  }
}
.navigation__container {
  position: absolute;
  right: 0;
  top: 63px;
  width: 100%;
  z-index: 100;

  @media (--not-narrow-vp) {
    position: relative;
    top: 0;
    width: 440px;
  }

  @media (--wide-vp) {
    top: 0;
    width: 478px;
  }
}
.main {
  color: var(--black-color);
  background-color: var(--white-color);
  counter-reset: section;
}
.catalog {
  display: flex;
  flex-direction: column;
}
.catalog__title {
  align-self: flex-start;
  margin: 0;
  padding: 0;
  margin-top: 27px;
  margin-bottom: 41px;
  position: relative;

  @include word-font ("Oswald", 36px, 36px);

  @media (--not-narrow-vp) {
    margin-top: 33px;
    margin-bottom: 72px;
    font-size: 60px;
    line-height: 60px;
  }

  @media (--wide-vp) {
    margin-top: 23px;
    margin-bottom: 82px;
  }

  &::before {
    @media (--narrow-vp) {
      content: "";
      position: absolute;
      top: -28px;
      left: -20px;
      height: 1px;
      width: 100vw;
      background-color: var(--button-color);
    }
  }

  &::after {
    @media (--narrow-vp) {
      content: "";
      position: absolute;
      bottom: -41px;
      left: -20px;
      height: 1px;
      width: 100vw;
      background-color: var(--button-color);
    }
  }
}
.catalog__product-container {
  margin-top: 20px;
  margin-bottom: 50px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 45px;

  @media (--not-narrow-vp) {
    margin-bottom: 68px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 68px;
    column-gap: 60px;
  }

  @media (--wide-vp) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 63px;
    column-gap: 80px;
    margin-top: 0;
    margin-bottom: 74px;
  }
}
.slogan {
  background: linear-gradient(var(--accent-trans-color) 302px, transparent 302px), url("../img/cat-bg-mobile@1x.webp"), linear-gradient(#4f4e54 302px, transparent 302px);
  background-repeat: repeat, no-repeat;
  background-position-x: center, center;
  background-position-y: top, top;
  position: relative;

  @media (min-resolution: 1.5dppx) {
    background: linear-gradient(var(--accent-trans-color) 302px, transparent 302px), url("../img/cat-bg-mobile@2x.webp"), linear-gradient(#4f4e54 302px, transparent 302px);
    background-size: auto, 320px, auto;
    background-repeat: repeat, no-repeat;
    background-position-x: center, center;
    background-position-y: top, top;
  }

  @media (--not-narrow-vp) {
    background-color: var(--white-color);
    background-image: none;
  }

  &::before {
    @media (--wide-vp) {
      content: "";
      position: absolute;
      width: 100%;
      bottom: 0;
      height: 200%;
      background:
        linear-gradient(to left, var(--accent-trans-color) 50%, var(--white-color) 50%),
        url("../img/cat-bg-desktop@1x.webp"),
        linear-gradient(to left, #4f4e54 50%, var(--white-color) 50%);
      background-repeat: repeat, no-repeat, repeat;
      background-position-x: center, right, center;
      background-position-y: top, bottom, top;

      @media (min-resolution: 1.5dppx) {
        background:
          linear-gradient(to left, var(--accent-trans-color) 50%, var(--white-color) 50%),
          url("../img/cat-bg-desktop@2x.webp"),
          linear-gradient(to left, #4f4e54 50%, var(--white-color) 50%);
        background-size: auto auto, 720px auto, auto auto;
        background-position-x: center, right, center;
        background-position-y: top, bottom, top;
        background-repeat: repeat, no-repeat, repeat;
      }
    }
  }
}
.slogan__container {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  padding-top: 27px;
  padding-bottom: 10px;

  @media (--not-narrow-vp) {
    padding-top: 33px;
    padding-left: 90px;
    padding-right: 90px;
    position: relative;
    min-height: 843px;
  }

  @media (--wide-vp) {
    padding-top: 60px;
    padding-left: 80px;
    padding-right: 80px;
    min-height: 530px;
  }
}
.slogan__title {
  color: var(--white-color);
  margin: 0;
  margin-bottom: 25px;
  text-align: center;
  max-width: 250px;

  @include word-font ("Oswald", 36px, 36px);

  @media (--not-narrow-vp) {
    max-width: 450px;
    text-align: left;
    color: var(--black-color);
    align-self: flex-start;
    margin-bottom: 41px;
    z-index: 2;
    font-size: 60px;
    line-height: 60px;
  }
}
.slogan__text {
  text-transform: uppercase;
  margin: 0;
  margin-bottom: 29px;
  color: var(--white-color);

  @include word-font ("Oswald", 14px, 14px);

  @media (--not-narrow-vp) {
    margin-bottom: 48px;
    color: var(--black-color);
    align-self: flex-start;
    z-index: 2;
    font-size: 20px;
    line-height: 20px;
  }

  @media (--wide-vp) {
    margin-bottom: 50px;
    width: 450px;
  }
}
.slogan__image {
  display: block;

  @media (--not-narrow-vp) {
    position: absolute;
    z-index: 1;
    bottom: 0;
    right: 50%;
    transform: translateX(50%);
    width: 709px;
    height: 609px;
  }

  @media (--wide-vp) {
    position: absolute;
    z-index: 1;
    left: 173px;
    top: -25px;
    width: 552px;
    height: 499px;
  }
}
.slogan__button {
  width: 100%;
  margin-top: 3px;
  margin-bottom: 10px;

  @media (--not-narrow-vp) {
    width: 244px;
    margin-top: 0;
    align-self: flex-start;
    z-index: 3;
  }

  @media (--wide-vp) {
    margin-bottom: 20px;
  }
}
.button {
  text-decoration: none;
  background-color: var(--accent-color);
  color: var(--white-color);
  text-transform: uppercase;
  text-align: center;
  padding-top: 10px;
  padding-bottom: 10px;
  display: block;

  @include word-font ("Oswald", 14px, 18px);

  @media (--not-narrow-vp) {
    padding-top: 17px;
    padding-bottom: 17px;
    font-size: 20px;
    line-height: 20px;
  }

  &:hover {
    background-color: var(--dark-accent-color);
    transition: background-color 0.3s ease;
  }

  &:not(:hover) {
    transition: background-color 0.3s ease;
  }

  &:active {
    background-color: var(--dark-accent-color);
    color: var(--white-trans-color);
  }

  &:focus-visible {
    border-radius: 2px;
    outline: 2px solid var(--accent-color);
    outline-offset: 3px;
  }
}
.programs {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;

  @media (--wide-vp) {
    padding-top: 82px;
    padding-bottom: 36px;
  }
}
.programs__list {
  margin: 0;
  padding: 0;
  list-style-type: none;

  @media (--wide-vp) {
    display: grid;
    grid-template-columns: 1fr 1fr;
    grid-column-gap: 80px;
    grid-row-gap: 40px;
  }
}
.programs__item {
  background-color: var(--footer-bg);
  position: relative;
  margin-bottom: 20px;
  padding-top: 30px;
  padding-left: 20px;
  padding-right: 20px;
  padding-bottom: 11px;

  @media (--not-narrow-vp) {
    margin-bottom: 30px;
    padding-top: 77px;
    padding-left: 52px;
    padding-right: 316px;
    padding-bottom: 30px;
  }

  @media (--wide-vp) {
    margin-bottom: 0;
    padding-right: 51px;
  }

  &:last-child {
    margin-bottom: 0;

    @media (--not-narrow-vp) {
      margin-bottom: 0;
    }
  }
}
.programs__title {
  text-transform: uppercase;
  display: block;
  padding-left: 71px;
  margin-bottom: 29px;

  @include word-font ("Oswald", 24px, 37px);

  @media (--not-narrow-vp) {
    padding-left: 0;
    margin-bottom: 24px;

    @include word-font ("Oswald", 36px, 36px);
  }

  @media (--wide-vp) {
    padding-left: 162px;
    margin-bottom: 73px;
  }
}
.programs__title--cat-sit::before {
  content: "";
  position: absolute;
  width: 50px;
  height: 53px;
  top: 21px;
  left: 22px;
  background-image: url("../icons/sprite.svg#cat-sit");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (--not-narrow-vp) {
    left: auto;
    right: 62px;
    width: 200px;
    height: 211px;
    top: 81px;
  }

  @media (--wide-vp) {
    left: 52px;
    right: auto;
    width: 100px;
    height: 106px;
    top: 41px;
  }
}
.programs__title--cat-walk::before {
  content: "";
  position: absolute;
  width: 67px;
  height: 50px;
  top: 24px;
  left: 13px;
  background-image: url("../icons/sprite.svg#cat-walk");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;

  @media (--not-narrow-vp) {
    left: auto;
    right: 28px;
    width: 268px;
    height: 200px;
    top: 91px;
  }

  @media (--wide-vp) {
    left: 35px;
    right: auto;
    width: 134px;
    height: 100px;
    top: 41px;
  }
}
.programs__text {
  color: var(--form-color);
  margin: 0;
  padding: 0;
  padding-bottom: 23px;
  border-bottom: 1px solid var(--border-color);

  @include word-font ("Arial", 14px, 18px);

  @media (--not-narrow-vp) {
    margin: 0;
    padding-bottom: 0;
    margin-bottom: 53px;
    border-bottom: none;

    @include word-font ("Arial", 16px, 24px);
  }

  @media (--wide-vp) {
    margin-bottom: 27px;
  }
}
.programs__link {
  text-decoration: none;
  display: inline-flex;
  align-items: center;
  margin-bottom: 11px;
  margin-top: 19px;
  margin-right: 10px;
  color: var(--black-color);
  text-transform: uppercase;
  position: relative;

  @include word-font ("Oswald", 16px, 16px);

  @media (--not-narrow-vp) {
    padding: 0;
    margin-top: 0;
    margin-bottom: 27px;
    font-size: 20px;
    line-height: 30px;
  }

  &:active {
    opacity: 0.3;
  }

  &:focus-visible {
    border: none;
    border-radius: 2px;
    outline: 2px solid var(--accent-color);
    outline-offset: 3px;
  }

  &::after {
    content: "";
    width: 32px;
    height: 10px;
    margin-left: 15px;
    background-image: url("../icons/sprite.svg#arrow");
    background-repeat: no-repeat;
    background-size: cover;
    background-position-y: center;
    background-position-x: -8px;
    transition: background-position 0.1s ease-out;

    @media (--not-narrow-vp) {
      margin-left: 25px;
    }
  }

  &:hover::after {
    background-position-x: 0;
    transition: background-position-x 0.3s ease;
  }
}
.process {
  display: flex;
  flex-direction: column;
  padding-top: 20px;
  padding-bottom: 48px;
  box-sizing: border-box;
  position: relative;

  @media (--not-narrow-vp) {
    padding-top: 45px;
    padding-bottom: 36px;
  }

  @media (--wide-vp) {
    border-bottom: 0;
    margin-bottom: 0;
    padding: 0;
    padding-top: 35px;
  }

  &::before {
    @media (--middle-vp) {
      position: absolute;
      content: "";
      width: calc(100% + 60px);
      left: -30px;
      height: 1px;
      bottom: 0;
      background-color: var(--main-border-color);
    }
  }
}
.process__title {
  margin: 0;
  margin-bottom: 43px;
  padding: 0;

  @include word-font ("Oswald", 36px, 40px);

  @media (--not-narrow-vp) {
    margin-bottom: 71px;
    font-size: 60px;
    line-height: 60px;
  }

  @media (--wide-vp) {
    margin-top: 0;
    margin-bottom: 73px;
  }
}
.process__list {
  padding: 0;
  margin: 0;
  list-style-type: none;
  display: grid;
  grid-template-columns: 1fr;
  gap: 20px;

  @media (--not-narrow-vp) {
    grid-template-columns: 1fr 1fr;
    column-gap: 60px;
    row-gap: 68px;
  }

  @media (--wide-vp) {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    margin-right: 0;
    column-gap: 80px;
  }
}
.process__item {
  min-height: 60px;
  position: relative;

  @media (--not-narrow-vp) {
    min-height: 250px;
  }
}
.process__text {
  margin: 0;
  padding-left: 80px;
  color: var(--form-color);
  position: relative;
  z-index: 4;

  @include word-font ("Arial", 14px, 18px);

  @media (--not-narrow-vp) {
    padding-top: 111px;
    padding-left: 0;
    padding-right: 70px;
    font-size: 16px;
    line-height: 24px;
  }

  @media (--wide-vp) {
    padding-right: 0;
  }

  &::before {
    position: absolute;
    width: 60px;
    height: 60px;
    top: 50%;
    transform: translateY(-50%);
    left: 0;
    background-position: center, center;
    background-repeat: no-repeat;
    background-color: var(--accent-color);

    @media (--not-narrow-vp) {
      top: 0;
      transform: translateY(0);
      width: 80px;
      height: 80px;
    }
  }

  &::after {
    @media (--not-narrow-vp) {
      counter-increment: section;
      content: counter(section);
