.form__label--radio:focus-within .form__radio-mark::before {
  outline: 2px solid var(--accent-color);
  outline-offset: 5px;
}
.form__label--radio:hover .form__radio-mark::before {
  box-shadow: 0 0 1px 1px var(--radio-hover);
}
.form__label--checkbox:focus-within .form__checkbox-mark {
  outline: 2px solid var(--accent-color);
  outline-offset: 4px;
}
