@custom-media --narrow-vp (width <= 767.9px);
@custom-media --not-narrow-vp (width >= 768px);
@custom-media --middle-vp (768px <= width < 1279.9px);
@custom-media --wide-vp (width >= 1280px);
@custom-media --not-wide-vp (width <= 1279.9px);
:root {
  --white-color: #ffffff;
  --white-trans-color: rgba(255, 255, 255, 0.3);
  --black-color: #000000;
  --accent-color: #68b738;
  --accent-trans-color: rgba(104, 183, 56, 0.85);
  --dark-accent-color: #5eaa2f;
  --error-color: #ff8282;
  --dark-form-text-color: #222222;
  --form-color: #444444;
  --light-form-color: #cdcdcd;
  --slider-track-color: #dcdcdc;
  --border-color: #d9d9d9;
  --form-border-color: #e7e7e7;
  --main-border-color: #e6e6e6;
  --catalog-more-color: #d3d3d3;
  --footer-bg: #f2f2f2;
  --section-bg-color: #eaeaea;
  --button-color: #ebebeb;
  --social__link-color: #666666;
  --radio-hover: #bababa;
  --window-version: "mobile";
}
@media (--not-narrow-vp) {
  :root {
    --window-version: "tablet";
  }
}

@font-face {
  font-family: "Oswald";
  font-style: medium;
  font-weight: 500;
  src: url("../fonts/oswaldmedium.woff2") format("woff2"), url("../fonts/oswaldmedium.woff") format("woff");
  font-display: swap;
}
@font-face {
  font-family: "Oswald";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/oswaldregular.woff2") format("woff2"), url("../fonts/oswaldregular.woff") format("woff");
  font-display: swap;
}
img {
  max-width: 100%;
  height: auto;
}

.page {
  height: 100%;
}

.page__body {
  margin: 0;
  display: grid;
  grid-template-rows: min-content 1fr min-content;
  min-height: 100%;
  font-family: "Arial", "Helvetica", sans-serif;
  background: var(--white-color);
  color: var(--black-color);
  min-width: 320px;
  overflow-x: hidden;
}

.page__centerer {
  margin: 0 20px;
  box-sizing: border-box;
}
@media (--not-narrow-vp) {
  .page__centerer {
    margin: 0 30px;
  }
}
@media (--wide-vp) {
  .page__centerer {
    margin: 0 auto;
    width: 1220px;
  }
}

.page__hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  border: none;
  clip: rect(0 0 0 0);
}

.header {
  background-color: var(--white-color);
  color: var(--black-color);
}

.header__container {
  box-sizing: border-box;
  min-height: 66px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-wrap: wrap;
}
@media (--not-narrow-vp) {
  .header__container {
    flex-direction: row;
    min-height: 107px;
  }
}
@media (--wide-vp) {
  .header__container {
    min-height: 165px;
    box-sizing: border-box;
  }
}

.header__logo {
  z-index: 100;
  display: flex;
  align-items: center;
  min-height: 65px;
}
.header__logo:hover {
  opacity: 0.8;
  transition: opacity 0.2s ease;
}
.header__logo:not(:hover) {
  transition: opacity 0.2s ease;
}
.header__logo:active {
  opacity: 0.6;
}
.header__logo:focus-visible {
  border: none;
  border-radius: 2px;
  outline: 2px solid var(--accent-color);
  outline-offset: 2px;
}

@media (--not-narrow-vp) {
  .header__logo img {
    width: 174px;
    height: 52px;
  }
}
@media (--wide-vp) {
  .header__logo img {
    width: 202px;
    height: 60px;
  }
}

.header__logo-none {
  display: flex;
  align-items: center;
  width: 101px;
  height: 18px;
}
@media (--not-narrow-vp) {
  .header__logo-none {
    display: none;
  }
}

.header__logo-none img {
  margin-left: -17px;
  width: 101px;
  height: 18px;
}

.navigation__button {
  position: absolute;
  top: 23px;
  right: 20px;
  width: 24px;
  height: 18px;
  box-sizing: border-box;
  background-color: transparent;
  border: none;
  padding: 0;
  cursor: pointer;
}
@media (--not-narrow-vp) {
  .navigation__button {
    display: none;
  }
}

.navigation--closed .navigation__button::after {
  content: "";
  width: 24px;
  height: 2px;
  background-color: var(--black-color);
  position: absolute;
  top: 0;
  left: 0;
  box-shadow: 0 8px 0 0 var(--black-color), 0 16px 0 0 var(--black-color);
}

.navigation--opened .navigation__button::before, .navigation--opened .navigation__button::after {
  content: "";
  width: 25px;
  height: 2px;
  background-color: var(--black-color);
  position: absolute;
  top: 6px;
  left: 0;
}
.navigation--opened .navigation__button::before {
  transform: rotate(45deg);
}
.navigation--opened .navigation__button::after {
  transform: rotate(-45deg);
}

@media (--narrow-vp) {
  .navigation--closed .navigation__container {
    display: none;
  }

  .navigation--opened .navigation__container {
    display: block;
  }

  .navigation--nojs {
    width: 100%;
  }

  .navigation--nojs .navigation__container {
    position: static;
    display: block;
  }

  .navigation--nojs .navigation__button {
    display: none;
  }
}
.navigation__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  flex-direction: column;
  align-items: center;
  background-color: var(--white-color);
}
@media (--not-narrow-vp) {
  .navigation__list {
    background-color: transparent;
    flex-direction: row;
    column-gap: 33px;
    justify-content: space-between;
  }
}

.navigation__link {
  text-decoration: none;
  display: block;
  padding: 22px 20px 24px 20px;
  color: var(--black-color);
  text-transform: uppercase;
  position: relative;
  font-family: "Oswald", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 20px;
  font-weight: 500;
}
@media (--not-narrow-vp) {
  .navigation__link {
    padding-left: 0;
    padding-right: 0;
    padding-top: 10px;
    padding-bottom: 10px;
    font-family: "Oswald", "Helvetica", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 18px;
    line-height: 24px;
  }
}
@media (--wide-vp) {
  .navigation__link {
    color: var(--black-color);
    font-family: "Oswald", "Helvetica", sans-serif;
    font-style: normal;
    font-weight: normal;
    font-size: 20px;
    line-height: 30px;
  }
}
@media (--not-narrow-vp) {
  .navigation__link:hover {
    opacity: 0.6;
    transition: opacity 0.4s ease;
  }
}
@media (--not-narrow-vp) {
  .navigation__link:not(:hover) {
    transition: opacity 0.4s ease;
  }
}
@media (--not-narrow-vp) {
  .navigation__link:active {
    opacity: 0.3;
  }
}
.navigation__link:focus-visible {
  border-radius: 2px;
  outline: 2px solid var(--dark-form-text-color);
  outline-offset: 3px;
}

@media (--not-narrow-vp) {
  .navigation__link--active::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    bottom: 0;
    background-color: var(--accent-color);
  }
}
@media (--wide-vp) {
  .navigation__link--active::before {
    content: "";
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: var(--accent-color);
  }
}

@media (--narrow-vp) {
  .navigation__item {
    width: 100%;
    text-align: center;
    border-top: 1px solid var(--main-border-color);
  }
  .navigation__item:last-child {
    border-bottom: 1px solid var(--main-border-color);
  }
}

@media (--wide-vp) {
  .navigation__link--white {
    color: var(--white-color);
  }
}
.navigation__link--white::before {
  background-color: var(--accent-color);
}
@media (--wide-vp) {
  .navigation__link--white::before {
    background-color: var(--white-color);
  }
}

.navigation__container {
  position: absolute;
  right: 0;
  top: 63px;
  width: 100%;
  z-index: 100;
}
@media (--not-narrow-vp) {
  .navigation__container {
    position: relative;
    top: 0;
    width: 440px;
  }
}
@media (--wide-vp) {
  .navigation__container {
    top: 0;
    width: 478px;
  }
}

.main {
  color: var(--black-color);
  background-color: var(--white-color);
  counter-reset: section;
}

.catalog {
  display: flex;
  flex-direction: column;
}

.catalog__title {
  align-self: flex-start;
  margin: 0;
  padding: 0;
  margin-top: 27px;
  margin-bottom: 41px;
  position: relative;
  font-family: "Oswald", "Helvetica", sans-serif;
  font-style: normal;
  font-weight: normal;
  font-size: 36px;
  line-height: 36px;
}
@media (--not-narrow-vp) {
  .catalog__title {
    margin-top: 33px;
    margin-bottom: 72px;
    font-size: 60px;
    line-height: 60px;
  }
}
@media (--wide-vp) {
  .catalog__title {
    margin-top: 23px;
    margin-bottom: 82px;
  }
}
@media (--narrow-vp) {
  .catalog__title::before {
    content: "";
    position: absolute;
    top: -28px;
    left: -20px;
    height: 1px;
    width: 100vw;
    background-color: var(--button-color);
  }
}
@media (--narrow-vp) {
  .catalog__title::after {
    content: "";
    position: absolute;
    bottom: -41px;
    left: -20px;
    height: 1px;
    width: 100vw;
    background-color: var(--button-color);
  }
}

.catalog__product-container {
  margin-top: 20px;
  margin-bottom: 50px;
  display: grid;
  grid-template-columns: 1fr;
  gap: 45px;
}
@media (--not-narrow-vp) {
  .catalog__product-container {
    margin-bottom: 68px;
    display: grid;
    grid-template-columns: 1fr 1fr;
    row-gap: 68px;
    column-gap: 60px;
  }
}
@media (--wide-vp) {
  .catalog__product-container {
    grid-template-columns: 1fr 1fr 1fr 1fr;
    row-gap: 63px;
    column-gap: 80px;
    margin-top: 0;
    margin-bottom: 74px;
  }
}

.slogan {
  background: linear-gradient(var(--accent-trans-color) 302px, transparent 302px), url("../img/cat-bg-mobile@1x.webp"), linear-gradient(#4f4e54 302px, transparent 302px);
  background-repeat: repeat, no-repeat;
  background-position-x: center, center;
  background-position-y: top, top;
  position: relative;
}
@media (min-resolution: 1.5dppx) {
  .slogan {
    background: linear-gradient(var(--accent-trans-color) 302px, transparent 302px), url("../img/cat-bg-mobile@2x.webp"), linear-gradient(#4f4e54 302px, transparent 302px);
    background-size: auto, 320px, auto;
    background-repeat: repeat, no-repeat;
    background-position-x: center, center;
    background-position-y: top, top;
  }
}
@media (--not-narrow-vp) {
  .slogan {
    background-color: var(--white-color);
    background-image: none;
  }
}
@media (--wide-vp) {
  .slogan::before {
    content: "";
    position: absolute;
    width: 100%;
    bottom: 0;
    height: 200%;
    background: linear-gradient(to left, var(--accent-trans-color) 50%, var(--white-color) 50%), url("../img/cat-bg-desktop@1x.webp"), linear-gradient(to left, #4f4e54 50%, var(--white-color) 50%);
    background-repeat: repeat, no-repeat, repeat;
    background-position-x: center, right, center;
    background-position-y: top, bottom, top;
  }
}
@media (--wide-vp) and (min-resolution: 1.5dppx) {
  .slogan::before {
    background: linear-gradient(to left, var(--accent-trans-color) 50%, var(--white-color) 50%), url("../img/cat-bg-desktop@2x.webp"), linear-gradient(to left, #4f4e54 50%, var(--white-color) 50%);
    background-size: auto auto, 720px auto, auto auto;
    background-position-x: center, right, center;
    background-position-y: top, bottom, top;
    background-repeat: repeat, no-repeat, repeat;
  }
}

.slogan__container {
  display: flex;
  flex-direction: column;
  align-items: center;
